import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Switch from "react-switch";
import Aux from "../../hoc/_Aux";
import ReactSelect from 'react-select';

import { validateVendorCreation } from '../../validation/vendor';
import { createVendor } from '../../api/inventory/vendorApi';

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getCountries } from '../../api/misc';
import isEmpty from '../../validation/isEmpty';

const Create = (props) => {


    const [submitting, setSubmitting] = React.useState(false);

    const [vendorData, setVendorData] = React.useState({
        fname: '',
        lname: '',
        mname: '',
        email: '',
        website: '',
        panNo: '',
        phone1: '',
        phone2: '',
        address: '',
        company: '',
        country: '',
        countryName: '',
    })
    const [countryOptions, setCountryOptions] = React.useState([]);
    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
        const controller = new AbortController();
        getCountries({ "action": "get" }, controller.signal)
            .then(res => {
                console.log(res);
                if (res.json.status) {
                    setCountryOptions([...res.json.countries.map(instance => ({ label: instance.name, value: instance.id }))])
                }
            })
            .catch(err => {
                console.log(err)
            })
        return () => controller.abort();
    }, [])


    const _selectedCountryCahnge = (country) => {
        console.log('here is the country', country)
        setErrors({ ...errors, 'country': '' })
        setVendorData({
            ...vendorData,
            country: country.value,
            countryName: country.label,
        })
    }

    const _vendorDataChange = (e) => {
        setErrors({ ...errors, [e.target.name]: '' });
        setVendorData({ ...vendorData, [e.target.name]: e.target.value })
    }


    const _submit = () => {
        //check if there are errors

        const { isValid, errors } = validateVendorCreation(vendorData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        console.log('no errros');

        setSubmitting(true)

        //if no errros api call
        const requestJson = {
            "action": "add",
            "first_name": vendorData.fname,
            "last_name": vendorData.lname,
            "middle_name": vendorData.mname,
            "email": vendorData.email,
            "website": vendorData.website,
            "tax_number": vendorData.panNo,
            "phone1": vendorData.phone1,
            "phone2": vendorData.phone2,
            "address": vendorData.address,
            "company": vendorData.company,
            "country": vendorData.country,
        }

        createVendor(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Vendor created successfully!', background: '#dffff0' })
                    try { props.history.push('/vendors') } catch (err) {
                        console.log(res.json.vendors[0]);
                        const createdInstance = res.json.vendors[0]
                        props.setCreatedToSelected({
                            value: createdInstance.id,
                            label: createdInstance.name
                        });
                        props.onHide();
                    }
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: "#ffe5d6" })
                    setSubmitting(false)
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }
    const _cancel = () => {
        try { props.history.push('/vendors') } catch (err) {
            props.onHide();
        }
    }




    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Vendor</h5>
                            <hr />
                            <Row>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>First Name*</Form.Label>
                                        <Form.Control name="fname" value={vendorData.fname} onChange={_vendorDataChange} type="text" placeholder="First Name" />
                                        <ErrorBody>{errors.fname}</ErrorBody>
                                    </Form.Group></Col>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>Middle Name</Form.Label>
                                        <Form.Control name='mname' value={vendorData.mname} onChange={_vendorDataChange} type="text" placeholder="Middle Name" />
                                        <ErrorBody>{errors.mname}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>Last Name*</Form.Label>
                                        <Form.Control name='lname' value={vendorData.lname} onChange={_vendorDataChange} type="text" placeholder="Last Name" />
                                        <ErrorBody>{errors.lname}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Company</Form.Label>
                                        <Form.Control name='company' value={vendorData.company} onChange={_vendorDataChange} type="text" placeholder="Company" />
                                        <ErrorBody>{errors.company}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Email*</Form.Label>
                                        <Form.Control name="email" value={vendorData.email} onChange={_vendorDataChange} type="email" placeholder="Email" />
                                        <ErrorBody>{errors.email}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Address</Form.Label>
                                        <Form.Control name='address' value={vendorData.address} onChange={_vendorDataChange} type="text" placeholder="Address" />
                                        <ErrorBody>{errors.address}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col xs={6} md={3}>
                                    <Form.Group  >
                                        <Form.Label>Phone No 1*</Form.Label>
                                        <Form.Control name='phone1' value={vendorData.phone1} onChange={_vendorDataChange} type="tel" placeholder="Phone No 1" />
                                        <ErrorBody>{errors.phone1}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col xs={6} md={3}>
                                    <Form.Group  >
                                        <Form.Label>Phone No 2</Form.Label>
                                        <Form.Control name='phone2' value={vendorData.phone2} onChange={_vendorDataChange} type="tel" placeholder="Phone No 2" />
                                        <ErrorBody>{errors.phone2}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>PAN/VAT Number</Form.Label>
                                        <Form.Control name='panNo' value={vendorData.panNo} onChange={_vendorDataChange} type="number" placeholder="PAN/VAT Number" />
                                        <ErrorBody>{errors.panNo}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Website</Form.Label>
                                        <Form.Control name='website' value={vendorData.website} onChange={_vendorDataChange} type="text" placeholder="Website" />
                                        <ErrorBody>{errors.website}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Country*</Form.Label>
                                        <ReactSelect
                                            value={isEmpty(vendorData.country) ? null : { label: vendorData.countryName, value: vendorData.country }}
                                            onChange={(instance) => _selectedCountryCahnge(instance)}
                                            options={countryOptions}
                                            placeholder='Select Country'
                                        />
                                        <ErrorBody>{errors.country}</ErrorBody>
                                    </Form.Group>
                                </Col>



                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={_cancel}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Create;
